#root .stable-select {
  padding-right: 0;
  min-width: 170px;
  .ant-select {
    padding-right: 20px;
    width: 100%;
    .ant-select-selector {
      width: 100%;
      padding-left: 16px;
      padding-right: 22px;
      background: transparent;
    }
    .token-logo {
      display: block;
      margin-right: 6px !important;
      width: 24px !important;
      height: 24px;
      border: 0;
    }
    .ant-select-selection-item {
      font-family: AvenirNext-Medium;
      line-height: 41px;
      font-size: 20px;
      font-weight: 300;
      color: #fff !important;
    }
    .ant-select-arrow {
      right: 17px;
    }
  }
  .ant-select-open {
    .ant-select-arrow {
      transform: rotate(180deg);
    }
  }
}

.stable-select-dropdown {
  padding: 0;
  border-radius: 10px;
  background: #2f2f41 !important;
  .ant-select-item-option-content {
    display: flex;
    align-items: center;
    font-family: AvenirNext-Medium;
    font-size: 18px;
    line-height: 30px;
    color: #fff !important;
  }
  .ant-select-item {
    margin: 0;
    border-radius: 10px;
    background: transparent;
    img {
      display: block;
      margin-right: 6px !important;
      width: 24px;
      height: 24px;
      border: 0;
    }
    &:hover {
      background: #5915e1 !important;
    }
  }
  // .ant-select-item-option-active {
  //   background: #5915e1 !important;
  // }
  .ant-select-item-option-selected {
    background: transparent !important;
    &:hover {
      background: transparent !important;
    }
  }
}

@media screen and (max-width: 1079px) {
  #root .stable-select {
    min-width: 128px;
    .ant-select {
      padding-right: 20px;
      .ant-select-selector {
        height: 38px;
        padding-left: 10px;
        padding-right: 12px;
      }
      .token-logo {
        width: 22px !important;
        height: 22px;
      }
      .ant-select-selection-item {
        line-height: 41px;
        font-size: 15px;
      }
      .ant-select-arrow {
        right: 14px;
      }
    }
  }

  .stable-select-dropdown {
    border-radius: 6px;
    .ant-select-item-option-content {
      font-size: 16px;
      line-height: 30px;
      color: #fff !important;
    }
    .ant-select-item {
      border-radius: 6px;
      img {
        width: 22px;
        height: 22px;
      }
    }
  }
}
